.container {
  width: 50%;
  margin-bottom: 0.2em;
  padding:0.2rem;
}
.video {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  border: 1px solid lightgray;
  box-shadow: 3px 3px 5px 0px rgba(191, 191,191, 0.53);
  cursor: pointer;
  transition: transform 250ms ease-in;
}
.video:hover {
  transform: scale(1.02);
}

.thumbnail {
  width: 40%;
  height: 100%;
}
.metadata{
  margin-left: 0.2em;
}
.title,
.channel {
  font-size: 0.8rem;
}

.channel {
  font-size: 0.6rem;
}
